.game__direction-list {
    display: grid;
    grid-template-columns: repeat(5, 80px);
    grid-template-rows: repeat(2, 20px);
    grid-gap: 10px;
    justify-content: center;
    margin: 50px 0 0;
    padding: 0;
    list-style: none;
}

.game__direction-item {
    background-color: yellow;
    text-align: center;
    text-transform: uppercase;
}
