.game__list {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: repeat(3, 100px);
    grid-gap: 10px;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.game__item {
    background-color: yellow;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
}